
.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.fa-shopping-bag:before {
    content: "\f290";
}


img {
    max-width: 100%;
}

.shop-home-list {
    padding: 0;
    padding-bottom: 100px;
}

.shop-home-list .shop-section-title {
    margin-bottom: 20px;
}

.shop-home-list .shop-section-title h1 {
    font-size: 22px;
    margin-bottom: 0;
    text-transform: capitalize;
    position: relative;
    color: #2c2d3f;
    font-weight: 700;
    margin-bottom: 10px;
    padding-bottom: 10px;
}

.shop-home-list .shop-section-title h1::before {
    position: absolute;
    content: "";
    height: 2px;
    width: 50px;
    background: #F7941D;
    left: 0;
    bottom: -1px;
}

.shop-home-list .single-list {
    overflow: hidden;
    position: relative;
    margin-top: 30px;
    border: 1px solid #eee;
    padding: 10px;
}

.shop-home-list .single-list .list-image {
    position: relative;
}

.shop-home-list .single-list .list-image.overlay:before {
    background: #000;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
}

.shop-home-list .single-list .list-image.overlay:hover:before {
    opacity: 0.3;
    visibility: visible;
}

.shop-home-list .single-list .list-image .buy {
    height: 40px;
    width: 40px;
    line-height: 40px;
    font-size: 14px;
    color: #fff;
    background: #F7941D;
    border-radius: 100%;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -20px;
    margin-top: -20px;
    text-align: center;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
    transform: scale(0);
    opacity: 0;
    visibility: hidden;
}

.shop-home-list .single-list .list-image .buy:hover {
    background: #fff;
    color: #333;
}

.shop-home-list .single-list .list-image:hover .buy {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
}

.shop-home-list .single-list .no-padding {
    padding-right: 0px;
}

.shop-home-list .single-list img {
    width: 100%;
    height: 100%;
}

.shop-home-list .single-list .content {
    padding-top: 45px;
    padding-right: 12px;
}

.shop-home-list .single-list .content .title {
    line-height: 18px;
}

.shop-home-list .single-list .content .title a {
    font-size: 14px;
    font-weight: 600;
}

.shop-home-list .single-list .content a:hover {
    color: #F7941D;
}

.shop-home-list .single-list .content .price {
    margin-top: 15px;
    font-weight: 500;
    background: #f7941d;
    display: inline-block;
    color: #fff;
    padding: 2px 18px;
    border-radius: 30px;
    font-size: 14px;
    font-weight: 500;
}


/*! CSS Used from: https://wpthemesgrid.com/themes/free/eshop/css/responsive.css */

@media only screen and (min-width: 768px) and (max-width: 991px) {
 
    .shop-home-list .shop-section-title {
        margin-top: 20px;
    }
}

@media only screen and (max-width: 767px) {
  
    .shop-home-list .shop-section-title {
        margin-top: 20px;
    }
    .shop-home-list .single-list .content {
        padding: 0;
        padding: 20px;
        text-align: center;
    }
}

@media only screen and (max-width: 450px) {
   
    .shop-home-list {
        padding-top: 0;
    }
    .shop-home-list .shop-section-title {
        margin-top: 20px;
    }
    .shop-home-list .single-list .content {
        padding: 0;
        padding: 20px;
        text-align: center;
    }
}