.p-large {
    font: 400 1.125rem/1.75rem "Open Sans", sans-serif;
}

.header {
    background-color: #5f4dee;
}

.header .header-content {
    padding-top: 8rem;
    padding-bottom: 4rem;
    text-align: center;
}

.header .text-container {
    margin-bottom: 3rem;
}

.header .p-large {
    margin-bottom: 2rem;
    color: #f3f7fd;
}

.header .btn-solid-lg {
    margin-right: 0.5rem;
    margin-bottom: 1.125rem;
    margin-left: 0.5rem;
    border-color: #f3f7fd;
    background-color: #f3f7fd;
    color: #5f4dee;
}

.header .btn-solid-lg:hover {
    background: transparent;
    color: #f3f7fd;
}

.header-frame {
    margin-top: -1px;
    width: 100%;
    height: 2.25rem;
}

@media (min-width: 768px) {
    .header .text-container {
        margin-bottom: 4rem;
    }
    .header h1 {
        font-size: 3.5rem;
        line-height: 4.125rem;
    }
    .header .btn-solid-lg {
        margin-bottom: 0;
        margin-left: 0;
    }
    .header-frame {
        height: 5.5rem;
    }
}

@media (min-width: 992px) {
    .header .header-content {
        text-align: left;
    }
    .header .text-container {
        margin-top: 4rem;
        margin-bottom: 0;
    }
    .header .image-container {
        position: relative;
        margin-top: 3rem;
    }
    .header .image-container .img-wrapper {
        position: absolute;
        display: block;
        width: 470px;
    }
    .header-frame {
        height: 8rem;
    }
}

@media (min-width: 1200px) {
    .header .header-content {
        padding-top: 11rem;
        padding-bottom: 5rem;
    }
    .header .text-container {
        margin-top: 5.5rem;
        margin-right: 0.5rem;
    }
    .header .image-container {
        margin-top: 1rem;
        margin-left: 1.5rem;
    }
    .header .image-container .img-wrapper {
        width: 630px;
    }
    .header-frame {
        height: 9.375rem;
    }
}


